<template>
  <div class="main-container">
    <PlayerNavbar
      v-if="workspaceStore.state.workspace"
      :current-path="currentPath"
      :hide-login="hideLogin"
      :runner-data="workspaceStore.state.workspace"
      :loading="loading"
      @navigate="handleNavigation"
      @login-click="handleLoginClick"
      @logout-click="handleLogoutClick"
    />
    <section class="content">
      <RouterView />
    </section>
  </div>
</template>

<script lang="ts" setup>
import PlayerNavbar from '@/apps/player/PlayerNavbar.vue';
import { SidebarItem } from '@/services/runnerData';
import { computed, ref } from 'vue';
import { useRoute, useRouter } from 'vue-router';
import { useWorkspaceStore } from '../store/workspaceStore';

const router = useRouter();
const workspaceStore = useWorkspaceStore();

const currentPath = useRoute().path;

const loading = ref<string | null>(null);

const handleNavigation = async ({ path, id }: SidebarItem) => {
  loading.value = id;
  await router.push({
    path: `/${path}`,
  });
  loading.value = null;
};

const handleLogoutClick = () => {
  router.push({ name: 'playerLogin' });
};

const handleLoginClick = () => {
  router.push({ name: 'playerLogin' });
};

const hideLogin = computed(() => {
  return !!useRoute().meta.hideLogin;
});
</script>

<style lang="scss" scoped>
.main-container {
  display: flex;
  flex-direction: column;
  height: 100%;

  .content {
    width: 100%;
    flex: 1;
    overflow: hidden;
  }

  .centered {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
  }
}
</style>
